.inputImage {
  width: 23px;
  height: 23px;
  border-radius: 50%;
  background-color: cadetblue;
  border: solid 1px;
  cursor: "pointer";
  position: absolute;
  margin: 0 auto;
  padding: 0;
  z-index: 3;
  top: 22%;
  left: 42%;
  transform: translateX(-50%);
  transform: translateY(-50%);
}
