.loginform {
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
}
.name {
  padding: 0;
  height: 25px;
  width: 196px;
  text-align: center;
}
.kakao {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
}
.btn {
  width: 200px;
  height: 35px;
  background-color: #423630;
  margin-top: 10px;
  color: aliceblue;
  padding: 1px 5px;
}
.btn:hover {
  background-color: #594941;
  cursor: pointer;
}
.logoutform {
  display: flex;
  position: absolute;
  margin: 0 auto;
  padding-bottom: 3px;
  z-index: 3;
  top: 22%;
  left: 30%;
  transform: translateX(-50%);
  transform: translateY(-50%);
  z-index: 3;
}
.outbtn {
  background-color: rgba(255, 0, 0, 0);
  border: 0px;
}
.arrow {
  width: 35px;
  height: 23px;
  background-color: rgba(255, 0, 0, 0);
  cursor: pointer;
}
